@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.App {
  text-align: center;
}




.login-page{
    background-color: #121212;
   background-image: url(/static/media/signup-bg.34f1d702.svg);
    background-size:100%;
    background-position:center;
    height: 100vh;
    background-repeat:no-repeat;
    display:flex;
    font-family: 'Montserrat', sans-serif;
   
}
.login-container{
    margin: 0 auto;
    display:flex;
    flex-direction: column;
    
}
.login-form{
    padding:50px;
    margin: auto;
}
.login-title{
    font-size: 60px;
    font-weight: bold;
    margin-bottom:20px;
    margin-top:40px;
    
}
.sub-text-container{
    color:white;
    font-size:20px;
    text-align: center;
    margin-bottom:30px;
}
.login-title-container{
    text-align: center;
    
}
.login-button{
    font-size:20px;
    border-radius: 40px;
    background-color:#1DB954;
    color: white;
    padding: 20px;
    padding-left: 30px;
    padding-right: 30px;
    font-weight:bold;
    border: none;
    outline:none;
  
}
.button-container{
    text-align: center;
}

.login-button:hover{
    color:black;
    cursor:pointer;
}

.queue-page{
    display:flex;
    height:100vh;
    background-color:#121212;
    background-image:url(/static/media/music.ccb133af.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
}
input:focus::-webkit-input-placeholder {
    color: transparent;
  }
input:focus:-ms-input-placeholder {
    color: transparent;
  }
input:focus::placeholder {
    color: transparent;
  }

.queue-container{
    margin: auto;
    display:flex;
    flex-direction: column;
    width:100%;
}
.title-container{
    text-align:center;
}
.form-container{
    margin-top: 20px;
    display:flex;
    flex-direction: column;

}
.information-container{  
    display:flex;
    flex-direction:column;
    margin-right:auto;
    margin-left: auto;
}
.list-container{
    padding:10px;
    background-color:black;
    width:500px;
    border-radius:20px;
    height:30vh;
    border-color: black;
    border: 1px solid black;
    margin-top:20px;
    margin-left:auto;
    margin-right:auto;
    text-align: left;
    overflow-y: auto;
    overflow-x: hidden;
}
.list-container::-webkit-scrollbar-thumb{
border-radius: 10px;
  
}
.list-container::-webkit-scrollbar-track{
    margin-top:20px;
    margin-bottom: 20px;
    border-radius: 10px;
   
}
td{
    padding: 10px;
    color:white;
}

.add-button{
    margin-top: 20px;
    margin-bottom: 20px;
    width:400px;
    font-size:20px;
    border-radius: 40px;
    background-color:#1DB954;
    color: white;
    padding: 10px;
    font-weight:bold;
    border: none;
    outline:none;
    margin-left:auto;
    margin-right: auto;
}
.add-button:hover{
    color:black;
    background-color: #1ED760;
    cursor:pointer;
}
.add-button:active{
    background-color:#18ac4d
}
.song-text-field{
    font-size:18px;
    width:400px;
    outline: none;
    border-top-style: hidden;
    border-right-style: hidden;
    border-left-style: hidden;
    border-color:white;
    margin-left:auto;
    
    margin-right: auto;
    background-color: transparent;
    box-sizing:boder-box;
}
input[type=text]{
    color:white;
}
::-webkit-input-placeholder{
    color:white;
    opacity:0.6;
    
}
:-ms-input-placeholder{
    color:white;
    opacity:0.6;
    
}
::placeholder{
    color:white;
    opacity:0.6;
    
}
.random-button{
    
    width:400px;
    font-size:20px;
    border-radius: 40px;
    background-color:white;
    color: #1DB95e;
    padding: 10px;
    font-weight:bold;
    border: none;
    outline:none;
    margin-left:auto;
    margin-right: auto;
}
.random-button:hover{
    color: black;
    cursor:pointer;
}
.random-button:active{
    background-color: whitesmoke;
}
h1{
    font-weight:bold;
    font-size:50px;
    margin-bottom:10px;
    color:white;
}
.listener-text{
    font-size:15px;
    color: white;
}
.queue-title{
    font-size:15px;
    color:#1DB95e;
}
.text-field-container{
    text-align: center;
    flex-direction: column;

    width:400px;
    margin-left: auto;
    margin-right: auto;
}

ul{
    width:402px;
    list-style-type: none;
    position: absolute;
    border: 1px solid black;
    border-top-style: hidden;
    background-color: white;
    text-align:left;
    margin:0;
    overflow: scroll;
    overflow-x: hidden;
    height:20vh;
    padding:0;
}

ul::before{
    border:none;
}

li{
    padding: 10px 5px;
}

li:hover{
    cursor: pointer;
    background-color:lightgrey;
}
.error-label{
    color:red;
    margin-top:6px;
}
td{
    word-wrap:break-word;
}
table{
    width:100%;
}
.image-column{
    width:10%;
}
.track-column{
    width:40%;
    max-width:1px;
}
.artist-column{
    width:20%;
}
.name-column{
    width:20%;
}
